import React from 'react';
import './index.css';

import { Col, Row } from 'react-bootstrap';

import logo from '../../assets/logo.png';
import taskText from '../../assets/black-task-text.png';
import task from '../../assets/black-task.png';
import chn from '../../assets/black-task-chn.png';


const Reader = () => {

  return (

    <div className="reader-app">

      <div className="reader-container">

        <Row style={{padding: 0, margin: 0}} >

          <Col lg={4} md={4} sm={12} xs={12} style={{padding: 0, margin: 0}} >
            <div className="reader-container-left">
                <img alt="" className="reader-container-task-black" src={task} />
                <img alt="" className="reader-container-task-chn" src={chn} />
            </div>         
          </Col>

          <Col lg={7} md={7} sm={12} xs={12} style={{padding: 0, margin: 0}} >
            <div className="reader-container-right">
              <img alt="" className="reader-container-task-text" src={taskText} />
              <img alt="" className="reader-container-task-text-logo" src={logo} />
              <p className="reader-container-task-text-description">In this post-apocalyptic world, there are the "Whales", demon whales, endowed with various physical and magical abilities and who have at their command, thousands of whale soldiers, an army controlled by force and fear</p>
            </div>
          </Col>

        </Row>

      </div>
 
      <div className="reader-red-line">
        <p className="reader-red-line-text">1-1 page Kujira No Konton</p>
      </div>
          
    </div>

  );
};

export default (Reader);
