//import Header from "../../../../Site/src/components/Header";

import './index.css';
import card from '../../assets/card.png';
import aboutChn from '../../assets/about-chn.png';

import React from 'react';
import { Col, Row } from 'react-bootstrap';


const About = () => {

  return (

    <div className="about-app">

        <div className="about-container">

          <Row style={{padding: 0, margin: 0}} >

            <Col lg={5} md={5} sm={12} xs={12} style={{padding: 0, margin: 0}} >
              <div className="about-container-left">
                <div>
                    <p className="about-text-title"> ABOUT </p>
                    <p className="about-text-description"> First NFT manga with weekly publication for token's holders.</p>
                    <p className="about-text-description"> You need to buy one token to access the "Kujira No Konton" manga with your account.</p>
                    <p className="about-text-description"> Read the latest and previous publication in the manga section being a token holder.</p>
                </div>
              </div>         
            </Col>

            <Col lg={6} md={6} sm={12} xs={12} style={{padding: 0, margin: 0}} >
              <div className="about-container-right">
                  <img alt="" className="about-container-right-img" src={card} />
              </div>
            </Col>

          </Row>

        </div>

      <img alt="" className="about-container-chn" src={aboutChn} />
 
    </div>

  );
};

export default (About);
